import React from "react";
import "../styles/CertPage.css";
import Header from "./Header";
import Project from "./Project";
import certthree from "../images/3/certthree.jpg";
import randomQuoteGenerator from "../images/3/random-quote-generator.jpg";
import markdownPreviewer from "../images/3/markdown-previewer.jpg";
import drumMachine from "../images/3/drum-machine.jpg";
import javascriptCalculator from "../images/3/javascript-calculator.jpg";
import pomodoroClock from "../images/3/pomodoro-clock.jpg";

function CertThree() {
  return (
    <div className="container">
      <Header
        title="Front End Libraries"
        url="front-end-libraries"
        image={certthree}
      />

      <div className="flex3">
        <div className="flex1">
          <Project
            id="randomQuoteGenerator"
            live="https://boutchersj.github.io/fcc-fel1"
            repo="#"
            image={randomQuoteGenerator}
            alt="random quote generator"
            title="Random Quote Generator"
          />
          <Project
            id="markdownPreviewer"
            live="https://boutchersj.github.io/fcc-fel2"
            repo="#"
            image={markdownPreviewer}
            alt="markdown previewer"
            title="Markdown Previewer"
          />
          <Project
            id="drumMachine"
            live="https://boutchersj.github.io/fcc-fel3"
            repo="#"
            image={drumMachine}
            alt="drum machine"
            title="Drum Machine"
          />
        </div>
        <div className="flex2">
          <Project
            id="javascriptCalculator"
            live="https://boutchersj.github.io/fcc-fel4"
            repo="#"
            image={javascriptCalculator}
            alt="javascript calculator"
            title="JavaScript Calculator"
          />
          <Project
            id="pomodoroClock"
            live="https://boutchersj.github.io/fcc-fel5"
            repo="#"
            image={pomodoroClock}
            alt="pomodoro clock"
            title="Pomodoro Clock"
          />
        </div>
      </div>
    </div>
  );
}

export default CertThree;
