import React from "react";
import "../styles/CertPage.css";
import Header from "./Header";
import Project from "./Project";
import certone from "../images/1/certone.jpg";
import tribute from "../images/1/tribute.jpg";
import survey from "../images/1/survey.jpg";
import productLanding from "../images/1/product-landing.jpg";
import docsPage from "../images/1/docs-page.jpg";
import portfolio from "../images/1/portfolio.jpg";

function CertOne() {
  return (
    <div className="container">
      <Header
        title="Responsive Web Design"
        url="responsive-web-design"
        image={certone}
      />

      <div className="flex3">
        <div className="flex1">
          <Project
            id="tribute"
            live="https://boutchersj.github.io/tribute"
            repo="https://github.com/boutchersj/tribute"
            image={tribute}
            alt="tribute page"
            title="Tribute Page"
          />
          <Project
            id="survey"
            live="https://boutchersj.github.io/survey"
            repo="https://github.com/boutchersj/survey"
            image={survey}
            alt="survey form"
            title="Survey Form"
          />
          <Project
            id="productLanding"
            live="https://boutchersj.github.io/product-landing"
            repo="https://github.com/boutchersj/product-landing"
            image={productLanding}
            alt="product landing page"
            title="Product Landing Page"
          />
        </div>
        <div className="flex2">
          <Project
            id="docsPage"
            live="https://boutchersj.github.io/documentation"
            repo="https://github.com/boutchersj/documentation"
            image={docsPage}
            alt="documentation page"
            title="Documentation Page"
          />
          <Project
            id="portfolio"
            live="https://boutchersj.github.io/"
            repo="https://github.com/boutchersj/boutchersj.github.io"
            image={portfolio}
            alt="portfolio page"
            title="Personal Portfolio Webpage"
          />
        </div>
      </div>
    </div>
  );
}

export default CertOne;
