import React from "react";
import "../styles/CertPage.css";
import Header from "./Header";
import Project from "./Project";
import certtwo from "../images/2/certtwo.jpg";
import caesarsCipher from "../images/2/caesars-cipher.jpg";
import cashRegister from "../images/2/cash-register.jpg";
import palindromeChecker from "../images/2/palindrome-checker.jpg";
import romanNumeralConverter from "../images/2/roman-numeral-converter.jpg";
import telephoneNumberValidator from "../images/2/telephone-number-validator.jpg";

function CertTwo(props) {
  return (
    <div className="container">
      <Header
        title="JavaScript Algorithms & Data Structures"
        url="javascript-algorithms-and-data-structures"
        image={certtwo}
      />

      <div className="flex3">
        <div className="flex1">
          <Project
            id="caesarsCipher"
            live={false}
            repo="https://github.com/boutchersj/FCC_JS_finalAlgs/blob/master/caesars_cipher"
            image={caesarsCipher}
            alt="caesar's cipher"
            title="Caesar's Cipher"
          />
          <Project
            id="cashRegister"
            live={false}
            repo="https://github.com/boutchersj/FCC_JS_finalAlgs/blob/master/cash_register"
            image={cashRegister}
            alt="cash register"
            title="Cash Register"
          />
          <Project
            id="palindromeChecker"
            live={false}
            repo="https://github.com/boutchersj/FCC_JS_finalAlgs/blob/master/palindrome_checker"
            image={palindromeChecker}
            alt="palindrome checker"
            title="Palindrome Checker"
          />
        </div>
        <div className="flex2">
          <Project
            id="romanNumeralConverter"
            live={false}
            repo="https://github.com/boutchersj/FCC_JS_finalAlgs/blob/master/roman_numeral_converter"
            image={romanNumeralConverter}
            alt="roman numeral converter"
            title="Roman Numeral Converter"
          />
          <Project
            id="telephoneNumberValidator"
            live={false}
            repo="https://github.com/boutchersj/FCC_JS_finalAlgs/blob/master/telephone_number_validator"
            image={telephoneNumberValidator}
            alt="telephone number validator"
            title="Telephone Number Validator"
          />
        </div>
      </div>
    </div>
  );
}

export default CertTwo;

