import React from "react";

class Project extends React.Component {
  constructor(props) {
    super();
    this.state = {
      maxScreen: window.matchMedia("(max-width: 812px)"),
      minScreen: window.matchMedia("(min-width: 812px)")
    };
    this.maintainBlur = this.maintainBlur.bind(this);
    this.mobileBlur = this.mobileBlur.bind(this);
    this.removeBlur = this.removeBlur.bind(this);
  }
  mobileBlur(event) {
    const { id } = event.target;
    const img = document.getElementById(id);
    if (this.state.maxScreen.matches) {
      // If media query matches
      img.classList.toggle("blur");
    }
  }
  removeBlur(event) {
    const { id } = event.target;
    const img = document.getElementById(id);
    const links = document
    .getElementById(id)
    .parentElement.querySelectorAll("a");

      img.classList.contains("blur") && img.classList.remove("blur");
      for (let i = 0; i < links.length; i++) {
        links[i].classList.contains('hidden') === false && links[i].classList.add('hidden');
      }
  }
  maintainBlur(event) {
    const { id } = event.target;
    const img = document.getElementById(id).parentElement.firstChild;
    const links = document
    .getElementById(id)
    .parentElement.querySelectorAll("a");
      img.classList.contains("blur") === false && img.classList.add("blur");
      for (let i = 0; i < links.length; i++) {
        links[i].classList.contains('hidden') && links[i].classList.remove('hidden')
      }

  }
  render() {
    return (
      <div className="project">
        <div className="project-container">
          <img
            id={this.props.id}
            className="thumbnail"
            src={this.props.image}
            alt={this.props.alt}
            onTouchStart={this.mobileBlur}
            onMouseOver={this.maintainBlur}
            onMouseOut={this.removeBlur}
          />
          <a
            id={`${this.props.id}-repo`}
            className={this.props.live ? "repo hidden" : "script hidden"}
            href={this.props.repo}
            target="_blank"
            rel="noopener noreferrer"
            onMouseOver={this.maintainBlur}
            onMouseOut={this.maintainBlur}
          >
            View Repo
          </a>
          {this.props.live && (
            <a
              id={`${this.props.id}-live`}
              className="live hidden"
              href={this.props.live}
              target="_blank"
              rel="noopener noreferrer"
              onMouseOver={this.maintainBlur}
              onMouseOut={this.maintainBlur}
            >
              View Live
            </a>
          )}
        </div>
        <h4 className="bumbletext">{this.props.title}</h4>
      </div>
    );
  }
}

export default Project;